import { isNumeric } from './index';

const DEFS = Object.seal({}); // faster, see http://jsben.ch/GRZ7q

export default function validateDate (d = DEFS, props, output, errors, path) {
    const { name, transform, required } = props;
    const { day, month, year } = d;

    if (day && !isNumeric(day)) {
        errors.push({ name, path, required, error: 'invalidDay' });
        return;
    }
    if (month) {
        const m = parseInt(month,10);
        if (!isNumeric(month) || m<1 || m>12) {
            errors.push({ name, path, required, error: 'invalidMonth' });
            return;
        }
    }
    if (year && !isNumeric(year)) {
        errors.push({ name, path, required, error: 'invalidYear' });
        return;
    }

    if (day && month && year) {
        // todo is it a valid date
        const date = { day, month, year };
        output[name] = transform ? transform(date) : date;
        return true;
    }

    if (required) {
        errors.push({ name, path, required, error: 'dateRequired' });
    }
}



