import React from 'react';

export default function PageLink ({ id, url, ttl }) {

    function doLink () {
        window.history.pushState(null,ttl,url);

        try {
            const popStateEvent = new PopStateEvent('popstate', {state: {}});
            dispatchEvent(popStateEvent);
        } catch (err) {
            const ev = document.createEvent("Event");
            let doesnt_bubble = false;
            let isnt_cancelable = false;
            ev.initEvent("popstate", doesnt_bubble, isnt_cancelable);
            dispatchEvent(ev);
        }
    }

    return (
        <button onClick={doLink}>{url}</button>
    );
}



